import { Component, SimpleChanges } from "@angular/core";
import { ShoppingService } from "./service/shopping.service";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.scss"]
})
export class AppComponent {
  private checkoutItems:any;
  private sum:number;
  private itemAmount:Array<number> = [];

  constructor(private shoppingItems: ShoppingService) {}

  private cartNum: number = 0;

  ngDoCheck(changes: SimpleChanges): void {
    let count = 0;
    this.shoppingItems.shoppingItems.forEach(ele => {
      count += ele.num;
      this.cartNum = count;
    });

        this.checkoutItems = this.shoppingItems.shoppingItems.filter(
      val => val.num > 0
    );
    this.sum = 0;
    this.shoppingItems.shoppingItems.forEach((val, idx) => {
      this.sum += val.price * val.num;
      this.itemAmount[idx] = val.num;
    });
  }
}



