import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Params } from "../../../node_modules/@angular/router";
import { GetProductService, Product } from "../service/get-product.service";
import { ShoppingService, ShoppingItem } from "../service/shopping.service";

@Component({
  selector: "app-product-detail",
  templateUrl: "./product-detail.component.html",
  styleUrls: ["./product-detail.component.scss"]
})
export class ProductDetailComponent implements OnInit {
  private productId: number;
  public products: any;
  // private shoppingItems:any;

  private product: Product;

  private productNumber: number = 1;

  public dataArr: any;

  constructor(
    private routeInfo: ActivatedRoute,
    private getProduct: GetProductService,
    private shoppingItems: ShoppingService
  ) {}

  ngOnInit(): void {}

  ngDoCheck(): void {
    this.routeInfo.params.subscribe(
      (params: Params) => (this.productId = params["id"])
    );

    this.productId = this.routeInfo.snapshot.params["id"];

    this.products = this.getProduct.products;

    this.product = this.products[this.productId];
  }

  //get json data from .json file
  // getHomeInfo() {
  //   this.getProduct.getProduct().subscribe(
  //     res => {
  //       this.products = res.json();
  //       this.dataArr = this.products;
  //       console.log(this.products);
  //       this.products.forEach((val, idx) => {
  //         this.products[idx] = val;
  //         console.log(val.id);
  //         console.log(val.title);
  //       });
  //     },
  //     error => {
  //       console.log(error);
  //     }
  //   );
  // }

  //change the number in the box
  addNum(num: number) {
    this.productNumber =
      this.productNumber <= 0 ? 0 : (this.productNumber += num);
  }

  //add products to shopping cart
  addProductToCart(id) {
    this.shoppingItems.shoppingItems[id].num += this.productNumber;
    console.log(this.shoppingItems.shoppingItems[id].num);
  }
}



