import { Component, OnInit, SimpleChanges } from "@angular/core";
import { ShoppingService } from "../service/shopping.service";

@Component({
  selector: "app-shopping-cart",
  templateUrl: "./shopping-cart.component.html",
  styleUrls: ["./shopping-cart.component.scss"]
})
export class ShoppingCartComponent implements OnInit {
  private checkoutItems: any;
  private sum: number = 0;
  private itemAmount: Array<number> = [];

  constructor(public shoppingItems: ShoppingService) {}

  ngOnInit() {}
  ngDoCheck(changes: SimpleChanges): void {
    this.checkoutItems = this.shoppingItems.shoppingItems.filter(
      val => val.num > 0
    );
    this.sum = 0;
    this.shoppingItems.shoppingItems.forEach((val, idx) => {
      this.sum += val.price * val.num;
      this.itemAmount[idx] = val.num;
    });
  }

  addNum(idx: number, num: number) {
    this.shoppingItems.shoppingItems[idx].num += num;
  }
}



